.services {
    height: 70px;
    display: flex;
    justify-content: center;
    &__individuals, &__company {
        width: 265px;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0px 3px 16px #00000029;
        cursor: pointer;
        &:hover {
            box-shadow: none;
            background: $main-color;
            color: #fff; } } }
